.cartContainer {
    width: 100vw;
    height: 50px;
    display: flex;
    justify-content: flex-end;
}
  
    .buttonCartContainer {
      position: absolute;
      margin-top: 10px;
      margin-right: 20px;
      cursor: pointer;
      z-index: 2;

    }
  
    .buttonCartContainer svg {
        height: 25px;
        margin-right: 2px;
        
    }
  
      .buttonCart {
        background-color: #222222;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 999px;
        
      }
      .productsNumber {
        height: 20px;
        width: 20px;
        border-radius: 999px;
        position: absolute;
        top: 0;
        right: 0;
        font-size: 12px;
        font-weight: bold;
        color: blue;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: white;
      }

  
    .cart {
      position: absolute;
      margin-top: 10px;
      margin-right: 20px;
      border-radius: 30px;
      width: 400px;
      background-color: #222222;
      color: white;
    }
  
    .cart h2 {
        text-align: center;
        font-weight: bold;
        margin-top: 35px;
      }
  
      .cartVacio {
        text-align: center;
        margin-top: 5px;
        font-size: 14px;
      }
  
      .total {
        text-align: center;
        font-weight: bold;
        margin-top: 15px;
        padding-bottom: 35px;
      }
  
      .productsContainer{
        max-height: 200px;
        overflow-y: scroll;
        margin-top: 5px;
      }

  
  ::-webkit-scrollbar {
    -webkit-appearance: none;
  }
  
  ::-webkit-scrollbar:vertical {
    width: 3px;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #797979;
    border-radius: 20px;
  }